html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  line-height: 1.5;
  height: 100vh;
  display: flex;
  flex-direction: column;
}

h2 {
  margin: 1rem;
  text-align: center;
  font-size: 2rem;
  color: #0000008C;
}
@media (min-width: 300px) {
  h2 {
    margin: 2rem;
    font-size: 2.25rem;
  }
}
@media (min-width: 992px) {
  h2 {
    font-size: 2.5rem;
  }
}

h3,
label,
.modal-title,
.btn {
  font-size: 1.6rem;
}
@media (min-width: 768px) {
  h3,
label,
.modal-title,
.btn {
    font-size: 1.75rem;
  }
}
@media (min-width: 992px) {
  h3,
label,
.modal-title,
.btn {
    font-size: 2rem;
  }
}

p,
.form-control,
.modal-body,
.invalid-feedback,
.thanks-home {
  font-size: 1.35rem;
}
@media (min-width: 768px) {
  p,
.form-control,
.modal-body,
.invalid-feedback,
.thanks-home {
    font-size: 1.5rem;
  }
}
@media (min-width: 992px) {
  p,
.form-control,
.modal-body,
.invalid-feedback,
.thanks-home {
    font-size: 1.75rem;
  }
}

.container {
  max-width: 120rem;
  padding: 0 2rem;
}

a {
  text-decoration: none;
}

img {
  width: 100%;
}

footer {
  margin-top: auto;
  text-align: center;
  padding-top: 1rem;
  padding-bottom: 1rem;
}

.navbar {
  height: 10rem;
}
@media (min-width: 576px) {
  .navbar {
    height: 8rem;
  }
}
.navbar img {
  height: 6rem;
  object-fit: contain;
}
@media (min-width: 576px) {
  .navbar img {
    height: 5rem;
    width: 18rem;
  }
}
.navbar a {
  margin-left: auto;
  margin-right: auto;
  font-size: 1.8rem;
  text-align: center;
}
@media (min-width: 992px) {
  .navbar li {
    padding-left: 3rem;
  }
  .navbar a {
    width: 16rem;
    font-size: 2.15rem;
  }
  .navbar img {
    height: 6rem;
  }
}
.navbar .container-fluid {
  padding-left: 2rem;
  padding-right: 2rem;
}

.navbar-light .navbar-nav .nav-link {
  transition: all 0.5s;
}
.navbar-light .navbar-nav .nav-link:hover {
  color: red;
}

.navbar-toggler {
  padding: 0.75rem;
}

.navbar-expand-sm .offcanvas {
  flex-grow: unset;
}

@media (min-width: 992px) {
  .offcanvas-start {
    width: unset;
  }
}

.offcanvas-header,
.offcanvas-body {
  padding-left: 2rem;
}

.offcanvas-title {
  font-size: 1.75rem;
}

.offcanvas-body {
  font-size: 1.5rem;
  display: flex;
  justify-content: end;
  align-items: flex-end;
}

.offcanvas-header {
  padding-top: 2rem;
}
.offcanvas-header .btn-close {
  padding-right: 2rem;
}

.btn {
  margin-top: 1rem;
}
@media (min-width: 768px) {
  .btn {
    padding: 0.5rem 15rem;
  }
}

.container form {
  margin-bottom: 2rem;
}
@media (min-width: 576px) {
  .container form {
    margin-left: 2rem;
    margin-right: 2rem;
  }
}
@media (min-width: 768px) {
  .container form {
    margin-left: 6rem;
    margin-right: 6rem;
  }
}
@media (min-width: 992px) {
  .container form {
    margin-left: 20rem;
    margin-right: 20rem;
  }
}

.form {
  margin-top: 1rem;
}
.form h2 {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .form {
    margin-bottom: 1rem;
  }
}

textarea {
  height: 10rem;
}

.btn-send {
  background-color: #d91318;
  color: white;
}

.form-check-input {
  width: 1.5rem;
  height: 1.5rem;
  margin-right: 0.5rem;
}

.form-check {
  display: flex;
  align-items: center;
}

.aviso {
  margin: 0;
}

.banner-img {
  object-fit: cover;
  height: 10%;
}
@media (min-width: 300px) {
  .banner-img {
    height: unset;
  }
}
@media (min-width: 992px) {
  .banner-img {
    height: 40rem;
  }
}
@media (min-width: 1200px) {
  .banner-img {
    height: 55vh;
  }
}

.carousel {
  display: none;
}
@media (min-width: 576px) {
  .carousel {
    display: block;
  }
}

svg {
  width: 4.5rem;
  height: 4.5rem;
  margin-left: 0.5rem;
  margin-right: 0.5rem;
}
@media (min-width: 992px) {
  svg {
    width: 6rem;
    height: 6rem;
  }
}

.social-networks {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 2rem;
}
.social-networks a {
  transition: all 0.5s;
}
.social-networks a:hover {
  transform: scale(1.25);
}

.map {
  margin-left: auto;
  margin-right: auto;
  height: 30rem;
  max-width: 120rem;
}
@media (min-width: 768px) {
  .map {
    height: 40rem;
    width: 40rem;
  }
}
@media (min-width: 992px) {
  .map {
    height: 50rem;
    width: 50rem;
  }
}

@media (min-width: 768px) {
  .location {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
  }
}

.location-img {
  display: none;
}
@media (min-width: 768px) {
  .location-img {
    height: 40rem;
    display: block;
    object-fit: cover;
  }
}
@media (min-width: 992px) {
  .location-img {
    height: 50rem;
  }
}

.whatsapp {
  position: fixed;
  width: 45px;
  height: 45px;
  bottom: 65px;
  right: 20px;
  background-color: transparent;
  color: #FFF;
  border-radius: 50px;
  text-align: center;
  font-size: 29px;
  z-index: 10000;
}
@media (min-width: 576px) {
  .whatsapp {
    font-size: 37px;
    right: 30px;
    width: 60px;
    height: 60px;
  }
}
@media (min-width: 992px) {
  .whatsapp {
    width: 70px;
    height: 70px;
    font-size: 44px;
  }
}

.whatsapp-icon {
  margin-top: 1px;
}

footer {
  background-color: #d91318;
  color: white;
}
footer p {
  margin: 0;
}

.grid-container {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2rem;
}
@media (min-width: 576px) {
  .grid-container {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 992px) {
  .grid-container {
    grid-template-columns: repeat(4, 1fr);
    gap: 3rem;
  }
}

.card-img-top {
  height: 10rem;
  object-fit: cover;
}
@media (min-width: 300px) {
  .card-img-top {
    height: 16rem;
  }
}
@media (min-width: 576px) {
  .card-img-top {
    height: 15rem;
  }
}
@media (min-width: 768px) {
  .card-img-top {
    height: 14rem;
  }
}

.card-body {
  text-align: center;
}

.card-text {
  text-align: center;
}

.slick-arrow {
  visibility: hidden;
}

.brand {
  height: 20rem;
}

.brand-container img {
  margin-left: auto;
  margin-right: auto;
  width: 80%;
  height: auto;
}
@media (min-width: 300px) {
  .brand-container img {
    width: 60%;
  }
}
@media (min-width: 576px) {
  .brand-container img {
    width: 60%;
  }
}

.brand-description {
  text-align: center;
  margin-top: 2rem;
  display: none;
}
@media (min-width: 768px) {
  .brand-description {
    display: block;
  }
}

.carousel-brands {
  margin-top: 3.5rem;
  margin-bottom: 2rem;
}

.wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
}

.checkmark__circle {
  stroke-dasharray: 166;
  stroke-dashoffset: 166;
  stroke-width: 4;
  stroke-miterlimit: 10;
  stroke: #ed3237;
  fill: none;
  animation: stroke 0.6s cubic-bezier(0.65, 0, 0.45, 1) forwards;
}

.checkmark {
  width: 5rem;
  height: 5rem;
  border-radius: 50%;
  display: block;
  stroke-width: 2;
  stroke: #fff;
  stroke-miterlimit: 10;
  margin: 10% auto;
  box-shadow: inset 0px 0px 0px #ed3237;
  animation: fill 0.4s ease-in-out 0.4s forwards, scale 0.3s ease-in-out 0.9s both;
}
@media (min-width: 768px) {
  .checkmark {
    width: 10rem;
    height: 10rem;
    margin: 5% auto;
  }
}

.checkmark__check {
  transform-origin: 50% 50%;
  stroke-dasharray: 48;
  stroke-dashoffset: 48;
  animation: stroke 0.3s cubic-bezier(0.65, 0, 0.45, 1) 0.8s forwards;
}

.thanks-msg {
  text-align: center;
  padding: 0 2rem;
  margin-bottom: 2rem;
}
.thanks-msg h2 {
  font-size: 3rem;
  font-weight: bold;
  margin: 0;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .thanks-msg h2 {
    font-size: 4rem;
    margin-bottom: 6rem;
  }
}
@media (min-width: 768px) {
  .thanks-msg {
    padding: 0 0;
    margin-bottom: 0;
  }
}

.thanks-phone {
  margin-top: 4rem;
}

.thanks-home {
  font-weight: bold;
}
.thanks-home:hover {
  color: #ed3237;
}

.thanks-section {
  margin-top: auto;
  margin-bottom: auto;
}

@keyframes stroke {
  100% {
    stroke-dashoffset: 0;
  }
}
@keyframes scale {
  0%, 100% {
    transform: none;
  }
  50% {
    transform: scale3d(1.1, 1.1, 1);
  }
}
@keyframes fill {
  100% {
    box-shadow: inset 0px 0px 0px 60px #ed3237;
  }
}
.thanks img {
  height: 10rem;
  object-fit: contain;
  margin-top: 0.1rem;
  padding-right: 2rem;
  padding-left: 2rem;
}
@media (min-width: 300px) {
  .thanks img {
    margin-top: 2rem;
  }
}