@use "base/mixins" as m;

.navbar {
  height: 10rem;
  @include m.telefono {
    height: 8rem;
  }
  img {
    height: 6rem;
    object-fit: contain;
    @include m.telefono {
      height: 5rem;
      width: 18rem;
    }
  }
  a {
    margin-left: auto;
    margin-right: auto;
    font-size: 1.8rem;
    text-align: center;
  }
  @include m.tablet {
    a {
      // width: 11rem;
    }
  }
  @include m.desktop {
    li {
      padding-left: 3rem;
    }
    a {
      width: 16rem;
      font-size: 2.15rem;
    }
    img {
      height: 6rem;
    }
  }
  .container-fluid {
    padding-left: 2rem;
    padding-right: 2rem;
  }
}

.navbar-light .navbar-nav .nav-link {
    transition: all 0.5s;
  &:hover {
      color: red;
  }
} 

.navbar-toggler {
  padding: 0.75rem;
}

.navbar-expand-sm {
  .offcanvas {
    flex-grow: unset;
  }
}

.offcanvas-start {
  @include m.desktop {
    width: unset;
  }
}

.offcanvas-header,
.offcanvas-body {
  padding-left: 2rem;
}

.offcanvas-title {
  font-size: 1.75rem;
}

.offcanvas-body {
  font-size: 1.5rem;
  display: flex;
  justify-content: end;
  align-items: flex-end;
}

.offcanvas-header {
  padding-top: 2rem;
  .btn-close {
    padding-right: 2rem;
  }
}
