@use 'base/mixins' as m;

.slick-arrow {
    visibility: hidden;
}

.brand {
    height: 20rem;
}

.brand-container {
    img {
        margin-left: auto;
        margin-right: auto;
        width: 80%;
        height: auto;
        @include m.telefonoSM {
            width: 60%;   
        }
        @include m.telefono {
            width: 60%;
        }
    }
}

.brand-description {
    text-align: center;
    margin-top: 2rem;
    display: none;
    @include m.tablet {
        display: block;
    }
}

.carousel-brands {
    margin-top: 3.5rem;
    margin-bottom: 2rem;
}