@use "base/mixins" as m;

.banner-img {
  object-fit: cover;
  height: 10%;
  @include m.telefonoSM {
    height: unset;
  }
  @include m.desktop {
    height: 40rem;
  }
  @include m.desktopXL {
      height: 55vh;
  }
}

.carousel {
  display: none;
  @include m.telefono {
    display: block;
  }
}
