@use "variables" as v;
@use "base/mixins" as m;

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  line-height: 1.5;
  height: 100vh;
  display: flex;
  flex-direction: column;
}

h2 {
  margin: 1rem;
  text-align: center;
  font-size: 2rem;
  color: #0000008C;
  @include m.telefonoSM {
    margin: 2rem;
    font-size: 2.25rem;
  }
  @include m.desktop {
    font-size: 2.50rem;
  }
}

h3,
label,
.modal-title,
.btn {
  font-size: 1.6rem;
  @include m.tablet {
    font-size: 1.75rem;
  }
  @include m.desktop {
    font-size: 2rem;
  }
}

p,
.form-control,
.modal-body,
.invalid-feedback,
.thanks-home
{
  font-size: 1.35rem;
  @include m.tablet {
    font-size: 1.50rem;
  }
  @include m.desktop {
    font-size: 1.75rem;
  }
}


.container {
  max-width: 120rem;
  padding: 0 2rem;
}

a {
  text-decoration: none;
}

img {
  width: 100%;
}

footer {
  margin-top: auto;
  text-align: center;
  padding-top: 1rem;
  padding-bottom: 1rem;
}
