@use 'base/mixins' as m;
@use 'base/variables' as v;

svg {
  width: 4.5rem;
  height: 4.5rem;
  margin-left: 0.5rem;
  margin-right: 0.5rem;
  @include m.desktop {
    width: 6rem;
    height: 6rem;
  }
}

.social-networks {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 2rem;
  a {
    transition: all 0.5s;
    &:hover {
      transform: scale(1.25);
    }
  }
}

.map {
  margin-left: auto;
  margin-right: auto;
  height: 30rem;
  max-width: 120rem;
  @include m.tablet {
    height: 40rem;
    width: 40rem;
  }
  @include m.desktop {
    height: 50rem;
    width: 50rem;
  }
}

.location {
  @include m.tablet {
    display: grid;
    grid-template-columns: repeat(2,1fr);
    gap: 1rem;
  }
}

.location-img {
    display: none;
    @include m.tablet {
      height: 40rem;
      display:block;
      object-fit: cover;
    }
    @include m.desktop {
      height: 50rem;
    }
}

.whatsapp {
  position:fixed;
  width:45px;
  height:45px;
  bottom:65px;
  right:20px;
  // background-color:#25d366;
  background-color:transparent;
  color:#FFF;
  border-radius:50px;
  text-align:center;
  font-size:29px;
  z-index:10000;
  @include m.telefono {
    font-size:37px;
    right:30px;
    width:60px;
    height:60px;
  }
  @include m.desktop {
    width: 70px;
    height: 70px;
    font-size: 44px;
  }
}

.whatsapp-icon {
  margin-top:1px;
}

footer {
  background-color: v.$primarioOscuro;
  color: white;
  p {
    margin: 0;
  }
}
