@use 'base/mixins' as m;

.grid-container {
    display: grid;
    grid-template-columns: repeat(1,1fr);
    gap: 2rem;
    @include m.telefono {
        grid-template-columns: repeat(3,1fr);
    }
    @include m.desktop {
        grid-template-columns: repeat(4,1fr);
        gap: 3rem;
    }
}

.card-img-top {
    height: 10rem;
    object-fit: cover;
    @include m.telefonoSM {
        height: 16rem;
    }
    @include m.telefono {
        height: 15rem;
    }
    @include m.tablet {
        height: 14rem;
    }
}

.card-body {
    text-align: center;
}

.card-text {
    text-align: center;
}