@use 'base/mixins' as m;
@use 'base/variables' as v;

.btn{
    margin-top: 1rem;
    @include m.tablet {
        padding: 0.5rem 15rem;   
    }
}

.container {
    form {
        // margin-left: 2rem;
        // margin-right: 2rem;
        margin-bottom: 2rem;
        @include m.telefono {
            margin-left: 2rem;
            margin-right: 2rem;
        }
        @include m.tablet {
            margin-left: 6rem;
            margin-right: 6rem;
        }
        @include m.desktop {
            margin-left: 20rem;
            margin-right: 20rem;
        }
    }
}

.form {
    margin-top: 1rem;
    h2 {
        margin-bottom: 0;
    }
    @include m.tablet {
        margin-bottom: 1rem;
    }
}

textarea{
    height: 10rem;
}

.btn-send {
    background-color: v.$primarioOscuro;
    color: white;
}

.form-check-input {
    width: 1.5rem;
    height: 1.5rem;
    margin-right: 0.5rem;
}

.form-check {
    display: flex;
    align-items: center;
}

.aviso {
    margin: 0;
}